                        //                     {value:"3",label:"实用",disabled:false},
                        //                     {value:"4",label:"有效",disabled:false},
                        //                 ])
                        //                 .className('myinput')
                        //                 .emit(['change'])
                        //                 .emitPrefix('gi'),

                                       
                        //             ]
                                    
                        //         },
        
                        //     ]
                        // },
                        // {
                        //     type:'el-carousel-item',
