                    props:{
                        type:'primary',
                        field:'prevbtn',
                        size:'mini',
                    },
                    domProps: {
                        innerHTML: '上一题'
                    },
                    on:{
                        click:this.PrevClick
                    }
                },
                {
                    type:'el-button',
                    field:'nextbtn',
                    col:{
                        span:12,
                      
                    },
                    style:{
                        float:'right'
                    },
                    props:{
                        type:'primary',
                        field:'nextbtn',
                        size:'mini',
                    },
                    domProps: {
                        innerHTML: '下一题'
                    },
                    on:{
                        click:this.NextClick
                    },
                }
            ],
            option:{
                resetBtn: false,
                submitBtn : false,
                btn:false,
                
            }
        }
    },
    computed:{
        myrule(){
        
            this.addLabelNumber(this.rule[0].children)
            return this.rule
        },
        totalnum(){
            return this.rule[0].children.length
            
        },
        countper(){
            if(this.totalnum===0){
                return 0

            }else{
                let t = (this.finishnum/this.totalnum)*100

                return t
            }
        }
    },
    methods:{
        addLabelNumber(data){
            for(let i=0;i<data.length;i++){
            
                data[i].props.label=i
            }
           
        },
        NextClick(){
           
            
            let t = document.querySelector('#testa').__vue__
            t.next()

            let component = $f.component()
            
            if(t.activeIndex==this.totalnum-1&&component.nextbtn.domProps.innerHTML=='下一题'){
                
                component.nextbtn.domProps.innerHTML='提交'    
                $f.sync('nextbtn')
                
                
            }else if(t.activeIndex==this.totalnum-1&&component.nextbtn.domProps.innerHTML=='提交'){
              
                this.postForm()
            }
            
            
        },
        PrevClick(){
            
            let t = document.querySelector('#testa').__vue__
            t.prev()
            let component = $f.component()
            if(component.nextbtn.domProps.innerHTML=='提交'){
                component.nextbtn.domProps.innerHTML='下一题'
                $f.sync('nextbtn')
            }
            
           
        },
        postForm(){
            let t = this.form
            this.$nextTick(()=>{
                //提交逻辑
                $f.submit((formData)=>{
                    this.$confirm(t, '', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'info',
                        customClass:'mytest'
                    }).then(() => {
                    this.$message({
                        type: 'success',
                        message: '提交成功'
                    });
                    }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消提交'
                    });          
                    });
                })
            })
        },
