            this.valueform=$f.formData()
            // this.getownform()
            // this.createownform()
        },
        createownform(config){
            // let t = $f.fields()
            // let gg = $f.formData()
            // let target =[]
            // for(let i=0;i<t.length;i++){
            //     let word = t[i].split('_')[0]
            //     target.push(word)
            // }
            // target = Array.from(new Set(target))
            
            // let obj={}
            // for(let i=0;i<target.length;i++){
            //     obj[target[i]]=[]
            // }

            
            // for(let i=0;i<t.length;i++){
            //     let nn = t[i].split('_')[0]
            //     // debugger
            //     let kk =t[i]
                
            //     obj[nn].push(kk)
                
            // }

            // for(let item in obj){
            //     let op={}
            //     for(let i=0;i<obj[item].length;i++){
            //         op[obj[item][i]]=gg[obj[item][i]]
            //     }
            //     obj[item]=op
            // }
            
            // this.form=obj
            let target =[]
            for(let i=0;i<config.length;i++){
    
                let obj={
                    questiontext:config[i].title,
                    questionstring:config[i].field,
                    answergroup:this.copyAnswer(config[i].question)
                }
                target.push(obj)
            }

            this.form=target

        },
        copyAnswer(data){
            let finalanswer =[]
            
            for(let i =0;i<data.length;i++){
                let obj={}
               

                    
                obj[i]=this.copyAnswerDetail(data[i].content)
                console.log(obj[i])
                    
                finalanswer.push(obj)
                
            }

           

            return finalanswer
        },
        copyAnswerDetail(data){
            let target
            if(typeOf(data)=='array'){
                target =[]
                for(let j=0;j<data.length;j++){
                    let obj={}
                    obj[data[j].field]=data[j].value
                    target.push(obj)
                }
                return target
            }else if(typeOf(data)=='object'){
                target={}
                target[data.field]=data.value
                return target
            }
            
        },
        countPercentage(data){
            
            // for(let i=0;i<data.length;i++){
            //     debugger
            //     if(data[i]!=''||data[i].length>0){
            //         debugger
            //         this.finishnum+=1
            //     }else{
            //         this.finishnum-=1
            //     }
            // }


            // let t =0
            // for(let item in data){
            //     if(data[item]!=''||data[item].length>0){
            //         t+=1
            //     } 
            // }
            // this.finishnum=t
        },
        createRule(config){
            let target=[]
            for(let i=0;i<config.length;i++){
                let obj={
                    type:'el-carousel-item',
                    props:{
                        header:config[i].title
                    },
                    children:this.initQuestion(config[i])
                }
                target.push(obj)
            }
            this.rule[0].children=target
        },
        initQuestion(data){
            let target =[]
            for(let i =0;i<data.question.length;i++){
                let obj={
                    type:'el-card',
                    props:{
                        header:data.question[i].title,
                        shadow:'never'
                    },
                    class:{
                        'question-card':true
                    },
                    children:this.initQuestionContent(data.question[i].content)
                }
                
                target.push(obj)
            }

            return target
        },
        initQuestionContent(data){
            let target = []
            if(typeOf(data)==='object'){
                target.push(data)
                return target
            }else if(typeOf(data)==='array'){
                return data
            }
        }
    },
    watch:{
        form:{
            handler(newval,oldval){
                if(oldval!=null){
                    this.countPercentage(newval)
                }else{
                    return 0
                }
            },
            deep:true
        }
