    }
    .myformcreate .el-form-item__label{
        width:100%;
        float: none;
    } */
    .el-message-box.mytest{
        width:80%;
        margin:10px auto;
    }
    .progress-wrapper{
        position: fixed;
        height: 50px;
        background: #fff;
        width:100%;
        z-index: 100;
    }
    .myformcreate.el-form{
        padding-top: 100px ;
    }
    .el-carousel__item{
        overflow: auto;
    }
</style>



