<template>
    <div style="position: relative;overflow:hidden">
        <form-builder></form-builder>
        <el-button @click="openCamera">123</el-button>
        <qrcode :value="aaa" :options="{ width: 200 }"></qrcode>
        
    </div>
</template>
<script>
import formBuilder from '../components/formBuilder'

export default {
    name:'home',
    components:{
        formBuilder,

    },
    mounted(){
        let appID = 'wx213b00ee9e767e01'
        let appsecret = '3c37407a007425f4b425606285c37095'
        let scope = 'snsapi_userinfo'
        let redirect_uri='https://nzy19940403.github.io/weixintest/dist/index.html'
        let response_type= 'code'
        
        this.aaa=`https://open.weixin.qq.com/connect/oauth2/authorize?appid=${appID}&redirect_uri=${redirect_uri}&response_type=${response_type}&scope=${scope}#wechat_redirect`

        // this.axios({
        //     url:'https://api.weixin.qq.com/cgi-bin/user/info?access_token=ACCESS_TOKEN&openid=OPENID&lang=zh_CN',
            
        // })
        // .then(res=>{
        //     debugger
        // })
        
    },
    data(){
        return {
            formdata:[
                {
                    type:'input',
                    field:'',

                    props:{
                        placeholder:'我试试'
                    }
                    
                }
            ],
            aaa:null,
        }
    },
    methods:{
        openCamera(){}
    }
}
</script>



